@mixin banner-position {
  position: relative;
}

@mixin banner-img-size {
  width: 100%;
  height: 223px;
  object-fit: cover;
  border-radius: 25px;
  filter: brightness(60%);

  @media (max-width: 600px) {
    height: 110px;
    border-radius: 10px;
  }
}

@mixin arrow-slider {
  position: absolute;
  top: 50%;
  transform: translate(0,-50%);
  z-index: 1;
  cursor: pointer;

  @media (max-width: 1024px) {
    height: 70px
  }

  @media (max-width: 600px) {
    height: 30px;
  }
}